      <v-list-item title="Agbetuntun"><v-btn :color="themeColor2" @click="hideDrawer" :to="{name: 'home'}" text><v-icon :color="themeColor">mdi-home</v-icon> Home</v-btn></v-list-item>
      <v-list-item title="About Agbetuntun"><v-btn :color="themeColor2" @click="hideDrawer" :to="{name: 'about'}" text><v-icon :color="themeColor">mdi-clipboard-text</v-icon> About</v-btn></v-list-item>
      <v-list-item title="Agbetuntun services"><v-btn :color="themeColor2" @click="hideDrawer" :to="{name: 'services'}" text><v-icon :color="themeColor">mdi-view-list</v-icon> Services</v-btn></v-list-item>
      <v-list-item title="Agbetuntun, request quotation"><v-btn :color="themeColor2" @click="hideDrawer" :to="{name: 'gallery'}" text><v-icon :color="themeColor">mdi-image-album</v-icon> Gallery</v-btn></v-list-item>
      <v-list-item title="Agbetuntun, contact us "><v-btn :color="themeColor2" @click="hideDrawer" :to="{name: 'contact'}" text><v-icon :color="themeColor">mdi-map-marker-radius</v-icon> Contact</v-btn></v-list-item>
